<template lang="html">
  <div class="balance-change">
    <el-form :model="formData" :rules="rules" ref="balanceForm" label-width="120px">
      <el-form-item prop="username" label="">
        <el-input v-model="formData.username" placeholder="" />
      </el-form-item>

      <el-form-item prop="gameType" label="">
        <el-select v-model="formData.gameType" placeholder="">
          <el-option v-for="item in gameTypes" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
      </el-form-item>

      <el-form-item prop="type" label="">
        <el-radio-group v-model="formData.type">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item prop="loanType" label="">
        <el-select v-model="formData.loanType" placeholder="">
            <el-option v-for="item in loanTypes" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
      </el-form-item>

      <el-form-item prop="money" label="">
        <el-input v-model="formData.money" type="number" min="0" placeholder="" />
      </el-form-item>

      <el-form-item prop="remark" label="">
        <el-input v-model="formData.remark" type="textarea" :rows="3" placeholder="" />
      </el-form-item>

      <el-form-item>
        <el-button @click="submitForm('balanceForm')" type="primary" size="medium"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'LoginUsername',
  data () {
    return {
      loanTypes: [],
      gameTypes: [],
      formData: {
        username: '', gameType: '', type: '', loanType: '', money: 0, remark: ''
      },
      rules: {
        username: [
          { required: true, message: '' },
          { min: 2, max: 20, message: ' 2 - 20 ' }
        ],
        gameType: { required: true, message: '' },
        type: { required: true, message: '' },
        loanType: { required: true, message: '' },
        money: [
          { required: true, message: '' }
          // { required: true, message: '0' }
        ]
      }
    }
  },
  created () {
    this.fetchGamesList()
    this.fetchFinanceLoanList()
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.saveFinanceBalanceChange(this.formData).then(() => {
            this.$router.push({ name: 'FinanceDepositForm' })
            this.$message.warning('')
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    },
    fetchGamesList () {
      this.$httpAPI.fetchGamesList().then(response => {
        this.$_.forEach(response.data.data, item => {
          this.gameTypes.push({
            label: item.name,
            value: item.id
          })
        })
      }).catch(error => console.log(error))
    },
    fetchFinanceLoanList () {
      this.$httpAPI.fetchFinanceLoanList({ params: { type: 0 } }).then(response => {
        this.$_.forEach(response.data.data, item => {
          this.loanTypes.push({
            label: item.name,
            value: item.dictionaryId
          })
        })
      }).catch(error => console.log(error))
    }
  }
}
</script>
