<template lang="html">
  <div class="load-form">
    <el-form :model="formData" :rules="rules" label-width="120px" ref="loanForm">
      <el-form-item prop="name" label="">
        <el-input v-model="formData.name" placeholder="" />
      </el-form-item>

      <el-form-item prop="sort" label="">
        <el-input v-model="formData.sort" type="number" min="1" max="100" placeholder="" />
      </el-form-item>

      <el-form-item prop="remark" label="">
        <el-input v-model="formData.remark" type="textarea" :rows="3" placeholder="" />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('loanForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'LoadForm',
  props: {
    data: {
      type: Object,
      default () {
        return {
          type: 0,
          name: '',
          sort: 0,
          remark: ''
        }
      }
    }
  },
  data () {
    return {
      formData: Object.assign({}, this.data),
      rules: {
        name: [
          { required: true, message: '' },
          { max: 20, message: '20' }
        ],
        sort: [
          { required: true, message: '' }
          // { min: 0, max: 100, message: ' 0-100 '}
        ]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.createFinanceLoan(this.formData).then(() => {
            this.$emit('on-success', this.formData)
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>
